h2{
  text-decoration: underline;
  text-decoration-color: rgb(80, 0, 0);
  text-decoration-thickness: 6px;
}

body {
  width: 100%;
  overflow-x: hidden;
  overflow-y: hidden;
}

.wh {
  margin-top: 30px;
  background-color: rgba(255, 255, 255, 0.637);
  box-shadow: 0px 0px 50px;
  height: 100%;
  width: 80%;
  opacity: 0.8;
  position: relative;
  left: 10%;
  font-family: OCR A Std, monospace;
  font-weight: bolder;
  border-radius: 5px;
  animation-name: SlideIn;
  animation-duration: 1.5s;
  animation-iteration-count: 1;
  backdrop-filter: blur(0.2em);
}

@keyframes SlideIn{
  0%{
    margin-top: 510px; 
  }
  5%{
    margin-top: 460px; 
  }
  10%{
    margin-top: 430px; 
  }
  15%{
    margin-top: 400px; 
  }
  20%{
    margin-top: 370px; 
  }
  25%{
    margin-top: 340px; 
  }
  30%{
    margin-top: 310px;
  }
  35%{
    margin-top: 280px;
  }
  40%{
    margin-top: 250px;
  }
  45%{
    margin-top: 220px;
  }
  50%{
    margin-top: 190px;
  }
  55%{
    margin-top: 160px;
  }
  60%{
    margin-top: 130px;
  }
  65%{
    margin-top: 100px;
  }
  70%{
    margin-top: 90px;
  }
  75%{
    margin-top: 80px;
  }
  80%{
    margin-top: 70px;
  }
  85%{
    margin-top: 60px;
  }
  90%{
    margin-top: 50px;
  }
  95%{
    margin-top: 40px;
  }
  100%{
    margin-top: 30px;
  }
}

.wh:hover {
  opacity: 0.9;
}

.wh>a {
  margin: 20px;
  line-height: 100px;
  font-size: 40px;
  color: white;
}

.h {
  color: red;
